.header {
  background: transparent url("../img/headerbgpng.png") no-repeat center center;
  min-height: 100vh;
  background-size: cover;
}

.header .menu-top .navbar .nav-item .nav-link {
  color: #fff;
  font-family: 'Montserrat (O...';
  font-size: 15px;
  text-transform: uppercase;
  font-weight: 600;
}

.header .menu-top .navbar .nav-item .nav-link:hover {
  color: #fff;
}

.header .menu-top .navbar .nav-item .nav-link:after {
  content: "";
  display: block;
  height: 3px;
  background: #34363a;
  width: 0;
  transition: width 1s;
  margin: auto;
}

.header .menu-top .navbar .nav-item .nav-link:hover:after {
  width: 100%;
}

.header .title {
  display: flex;
  height: 100vh;
}

.header .title .item {
  margin: auto;
  text-align: center;
}

.clean {
  background: #34363a;
  padding: 210px 0;
}

.clean .item {
  display: flex;
  margin-bottom: 130px;
  text-align: right;
}

.clean .item h3 {
  font-size: 22px;
  font-weight: 400;
  color: #fff;
}

.clean .item p {
  font-size: 17px;
  font-weight: 300;
  color: #9da6ac;
}

.clean .icon {
  margin-left: 10px;
}

.clean .icon .fa {
  font-size: 30px;
  border: 1px solid #9da6ac;
  padding: 10px;
  border-radius: 50%;
  color: #fff;
}

.video {
  height: 450px;
  position: relative;
}

.video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.video .item {
  position: absolute;
  top: 40%;
  text-align: center;
  width: 100%;
}

.video .item .fa {
  font-size: 40px;
  color: #fff;
}

.video .item p {
  color: #fff;
  font-size: 30px;
}

.layout {
  padding-top: 100px;
  padding-bottom: 190px;
  background: #34363a;
}

.layout .layout-text {
  padding-top: 80px;
  padding-bottom: 80px;
}

.layout .layout-text h3 {
  font-family: 'Montserrat (O...';
  font-size: 40px;
  color: #fff;
  font-weight: 700;
  padding-bottom: 20px;
}

.layout .layout-text p {
  font-family: 'Montserrat (O...';
  font-size: 17px;
  color: #929aa0;
}

.layout .item {
  display: flex;
  padding-bottom: 20px;
}

.layout .item .layout-text-2 {
  margin-left: 10px;
  padding: 5px;
}

.layout .item h5 {
  font-family: 'Montserrat (O...';
  font-size: 17px;
  color: #fff;
  text-transform: uppercase;
}

.layout .item .fa {
  font-size: 40px;
  color: #ff3366;
}

.layout .item p {
  font-family: 'Montserrat (O...';
  font-size: 17px;
  color: #929aa0;
}

.third {
  padding-top: 115px;
  padding-bottom: 150px;
  background: #34363a;
}

.third .third-img img {
  height: 680px;
}

.third .third-text {
  padding-top: 110px;
  padding-bottom: 100px;
}

.third .third-text h3 {
  font-family: 'Montserrat (O...';
  font-size: 40px;
  color: #fff;
}

.third .third-text p {
  font-family: 'Montserrat (O...';
  font-size: 17px;
  color: #80878d;
}

.third .third-text p span {
  color: #ff3366;
}

.slider {
  background: #ff3366;
  padding: 150px 0;
}

.slider .owl-dots {
  text-align: center;
}

.slider .owl-dots .owl-dot {
  width: 15px;
  height: 15px;
  background: #ff5e86;
  border-radius: 50%;
  margin: 0 8px;
}

.slider .owl-dots .owl-dot.active {
  background: #fff;
}

.price {
  background: #34363a;
  padding: 120px 0;
}

.price .card {
  border: 2px solid transparent;
}

.price .card .card-header {
  position: relative;
}

.price .card .card-header .popular {
  position: absolute;
  right: 0;
  top: 25px;
  background: #ff3366;
  color: #fff;
  padding-right: 6px;
  padding-left: 14px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  text-transform: uppercase;
}

.price .card .card-header h4 {
  color: #585b60;
  font-size: 27px;
}

.price .card .card-header h5 {
  font-size: 17px;
  color: #6e737b;
}

.price .card .card-header p {
  color: #9da6ac;
  font-weight: 300;
  font-size: 15px;
}

.price .card .card-body ul {
  list-style: none;
  padding: 0;
}

.price .card .card-body ul li:before {
  content: "\2714";
  color: #ff3366;
  margin-right: 10px;
  font-size: 20px;
}

.price .card .card-footer h3 {
  font-family: 'Montserrat (O...';
  font-size: 20px;
  color: #9da6ac;
}

.price .card .card-footer h3 .usd {
  color: #585b60;
  font-weight: 700;
}

.price .card .card-footer h3 .number {
  font-size: 50px;
  color: #585b60;
  font-weight: 700;
}

.price .card .card-footer .item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price .card .card-footer .item .btn {
  background: #ff3366;
  color: #fff;
}

.price .card .card-footer .item p {
  margin: 0;
}

