}

.touch {
  padding-top: 135px;
  padding-bottom: 125px;
  background: #2d2f32;
}

.touch .touch-text h4 {
  font-family: 'Montserrat (O...';
  font-size: 40px;
  color: #fff;
}

.touch .touch-text p {
  font-family: 'Montserrat (O...';
  font-size: 19px;
