}

.touch .touch-input {
  display: flex;
  justify-content: center;
}

.touch .touch-input .form-control {
  width: 450px;
  margin: 0 0 15px;
  height: 48px;
  background: #34363a;
  color: #9da6ac;
  border: 1px solid #9da6ac;
}

.touch .touch-input .btn {
  width: 450px;
  background: #ff3366;
  color: #fff;
  text-align: center;
  height: 48px;
}

.touch .touch-input .sar .form-control {
  height: 190px;
}

.footer {
  padding-top: 100px;
  padding-bottom: 170px;
  background: #34363a;
}

.footer .icon img {
  margin: 0 5px;
  padding-bottom: 15px;
}

.footer .text {
  font-family: 'Montserrat (O...';
  font-size: 17px;
  color: #9da6ac;
}

.footer .text:before {
  content: "";
  display: block;
  height: 1px;
  width: 300px;
  background: #9da6ac;
  margin: auto;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  min-width: 320px;
  font-family: 'Montserrat';
  font-size: 17px;
}

h2 {
  font-size: 40px;
  color: #fff;
}

.card-active {
  border: 2px solid #ff3366 !important;
}

.fix {
  background: #ff3366;
  box-shadow: 0 10px 15px #000;
  position: fixed;
  width: 100%;
  z-index: 999;
  top: 0;
}

#backtotop {
  z-index: 999;
  display: inline-block;
  position: fixed;
  visibility: hidden;
  bottom: 20px;
  right: 20px;
  width: 36px;
  height: 36px;
  line-height: 36px;
  font-size: 16px;
  text-align: center;
  opacity: .2;
  background: #000;
}

#backtotop i {
  display: block;
  width: 100%;
  height: 100%;
  line-height: inherit;
}

#backtotop.visible {
  visibility: visible;
  opacity: .5;
}

#backtotop:hover {
  opacity: 1;
}

@media (max-width: 992px) {
  .header .menu-top .navbar .nav-item {
    text-align: center;
  }

  .header .menu-top .navbar .nav-item .nav-link:hover:after {
    width: 15%;
  }
}